.main-card {
  z-index: 2;
  display: flex;
  flex-flow: column wrap;
  align-content: space-between;
  font-family: 'Open Sans', sans-serif;
  padding-top: 146px;
  height: 610px;
  width: 1171px;
  @include media('<large') {
    flex-flow: column nowrap;
    height: auto;
    width: 456px;
    margin: auto;
    padding: 70px 0 70px; }
  @include media('<tablet') {
    width: 100%; }
  &__main-title {
    order: 0; }
  &__subtitle {
    margin-top: 36px;
    max-width: 45%;
    @include media('<large') {
      order: 1; } }

  &__text-wrapper {
    max-width: 526px;
    margin-top: 15px;
    min-height: 110px;
    display: flex;
    align-items: center;
    @include media('<large') {
      max-width: 100%;
      order: 2; } }
  &__text {
    @include resetmargin;
    z-index: 2;
    font-size: 1.5rem;
    line-height: 28px; }
  &__features-title {
    @include resetmargin;
    font-family: "Yeseva One", serif;
    font-size: 1.8rem;
    margin-top: 22px;
    max-width: 45%; }
  &__features {
    z-index: 2;
    @include media('<large') {
      order: 3; } }
  &__features-list {
    @include resetlist;
    z-index: 2;
    margin-top: 16px;
    display: flex;
    flex-flow: column wrap;
    align-content: space-between;
    max-height: 160px;
    width: 526px;
    @include media('<large') {
      max-height: unset; }
    &--wide {
      flex-flow: column nowrap;
      margin-bottom: -20px;
      .main-card__features-list-item {
        max-width: 100%; }
      @include media('<large') {
        margin-bottom: 0;
        max-height: unset; } }
    @include media('<large') {
      li:nth-child(n+5) {
        display: none; } } }
  &__features-list-item {
    display: block;
    position: relative;
    font-size: 1.5rem;
    line-height: 28px;
    padding-left: 14px;
    color: $dark-text;
    max-width: 55%;

    @include media('<large') {
      max-width: 100%; }
    &::before {
      position: absolute;
      left: 0;
      top: 12px;
      content: "";
      display: block;
      width: 5px;
      height: 5px;
      border: 1px solid $dark-text;
      border-radius: 50%;
      background: none; } }
  &__details {
    margin-top: auto;
    margin-bottom: 30px;
    max-height: 40px;
    display: flex;
    @include media('<large') {
      order: 4;
      max-width: 100%;
      margin-top: 30px; }
    @include media('<tablet') {
      flex-flow: row wrap;
      max-height: unset; } }
  &__detail {
    display: flex;
    align-items: center;
    margin-right: 20px;
    @include media('<tablet') {
      margin-top: 10px; } }
  &__detail-icon {
    height: 36px;
    width: 36px;
    background-color: $maincolor;
    background-repeat: no-repeat;
    background-position: 13px 13px;
    border-radius: 50%;
    &--ruble {
      background-image: url("../img/icon-ruble.svg"); }
    &--square-m {
      background-image: url("../img/icon-square-m.svg");
      background-position: 9px 12px; }
    &--clock {
      background-image: url("../img/icon-clock.svg");
      background-position: 9px 9px; } }
  &__detail-text {
    @include resetmargin;
    font-size: 1.8rem;
    font-family: "Yeseva One", serif;
    margin-left: 9px;
    text-transform: capitalize;
    @include media('<large') {
      font-size: 1.6rem; }
    sup {
      font-size: 1rem; } }

  &__interaction {
    display: flex;
    align-items: center;
    @include media('<large') {
      margin-top: 30px;
      order: 5;
      flex-flow: row wrap; } }
  &__book-btn {
    border: none;
    width: 256px;
    padding: 20px 20px;
    @include media('<large') {
      flex-basis: 100%; } }
  &__more-info {
    margin-left: 48px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 1.2rem;
    color: $black;
    text-decoration: none;
    @include media('<large') {
      flex-basis: 100%;
      margin-left: 0;
      margin-top: 20px; } }
  &__cursive-aside {
    z-index: 1;
    position: absolute;
    right: 150px;
    bottom: 250px;
    width: 100px;
    height: 300px;
    .cursive-aside-title {
      bottom: 0;
      left: 0;
      width: 10px;
      height: 10px; }
    &--rooms {}
    &--beauty {
      right: 150px; }
    &--food {}
    &--relax {} }




  &__slider {
    position: relative;
    width: 456px;
    height: 610px;
    margin-right: 52px;
    margin-left: auto;
    @include media('<large') {
      order: 2;
      margin-top: 35px;
      margin-left: 0; }
    @include media('<tablet') {
      width: 291px;
      height: 390px;
      margin: 30px auto 30px;
      left: -20px; }
    &--left-side {
      order: -1;
      margin-right: 0;
      margin-left: 52px;
      @include media('<large') {
        order: 2; }
      @include media('<tablet') {
        margin: 30px auto 30px;
        left: 20px; } }
    .swiper-wrapper {}
    .swiper-slide {
      height: 610px;
      width: 456px;
      @include media('<tablet') {
        width: 291px;
        height: 390px; }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center; } }


    &-next, &-prev {
      border: none;
      box-sizing: border-box;
      width: 52px;
      height: 52px;
      @include media('<tablet') {
        width: 44px;
        height: 44px; }
      &:after {
        font-size: 17px; }
      &:focus, &:active {
        outline: none; } }
    &-prev {
      &--left {
        right: auto;
        top: auto;
        left: -52px;
        bottom: 0;
        @include media('<tablet') {
          left: -44px; } }
      &--right {
        left: auto;
        top: auto;
        right: 0;
        bottom: 0; } }
    &-next {
      &--left {
        right: auto;
        top: auto;
        left: 0;
        bottom: 0; }
      &--right {
        left: auto;
        top: auto;
        right: -52px;
        bottom: 0;
        @include media('<tablet') {
          right: -44px; } } }
    &-pagination {
      max-width: 100px;
      transform: rotate(90deg);
      user-select: none;
      &--right {
        left: auto;
        bottom: auto;
        right: -95px;
        top: 41px;
        @include media('<tablet') {
          right: -75px; } }
      &--left {
        right: auto;
        bottom: auto;
        left: -95px;
        top: 41px;
        @include media('<tablet') {
          left: -75px; } } }
    &-pagination-inside {
      position: relative;
      display: flex;
      flex-flow: row nowrap;
      font-size: 1.4rem; }
    &-pagination-separator {
      margin: 10px 10px 0 10px;
      display: block;
      width: 54px;
      height: 1px;
      background-color: $black; } } }



